ektarbeit")])])]),_c('p',{staticClass:"footnote"},[_vm._v("\n      Autoren: Dennis Sterle und Hendrik Gregor Pawlowski\n    ")])]),_c('div',{staticClass:"content"},[_c('h1',{staticClass:"first-headline removeBorder"},[_vm._v("Cranach erweiterte Interaktion")]),_c('p',{staticClass:"hint-text"},[_vm._v("Willkommen zu unserem Projekt Cranach Erweiterte Interaktion! Dieses Projekt findet im Rahmen des Kurses \"Web-Development\" an der TH-Kln im Studiengang Medieninformatik statt.")]),_c('h2',{staticClass:"title-text jump-item",attrs:{"id":"kontext"}},[_vm._v("Kontext")]),_c('p',{staticClass:"hint-text"},[_vm._v("Der Kontext ist der ein Besuch in einem Museum ber Gemlde des Knstlers Lucas Cranach. Hierbei sollen dem Benutzer besondere Informationen ber diese Gemlde zur Verfgung gestellt werden.")]),_c('h2',{staticClass:"title-text jump-item",attrs:{"id":"idee"}},[_vm._v("Idee")]),_c('p',{staticClass:"hint-text"},[_vm._v("Nach etlichen Ideen ber die Richtungen, in die unser System gehen kann (Sie finden unsere Entscheidungen unter dem Reiter Wiki im Kapitel Entscheidungslogbuch) haben wir nun eine Anwendung erstellt, die es dem Benutzer erleichtern soll, Informationen ber das Gemlde, vor welchem er sich befindet, zu erhalten. Besonders wichtig war uns dabei der Gedanke, dass der Benutzer nicht extra eine native App auf seinem Smartphone installieren muss, da das mit einem zu groen Aufwand verbunden ist. Des Weiteren war ein wichtiger Punkt fr uns, dass die Gemlde im Museum eine grere Gewichtung haben als das Handy des Benutzers, denn in einem Museum mchte man nicht mit seinem Smartphone beschftigt sein, sondern man mchte sich auf die Bilder konzentrieren. Unsere Anwendung ist so konzipiert, dass man die Informationen ber bestimmte Gemlde ber seine Kopfhrer anhren kann und sein Handy dabei in der Hosentasche lassen kann. Natrlich kann man die Informationen aber auch visuell einsehen. Damit Sie sich ein besseres Bild davon machen knnen, folgt nun ein Szenario:")]),_c('h2',{staticClass:"title-text jump-item",attrs:{"id":"szenario"}},[_vm._v("Szenario")]),_c('p',{staticClass:"hint-text"},[_vm._v("Simon ist auf dem Weg zu einer Ausstellung ber den Knstler Lucas Cranach. In dem Museum angekommen, bekommt er an der Rezeption den WLAN-Schlssel und verbindet sich mit diesem. Nun ffnet er die Website des Lucas Cranach Museums. Auerdem bekommt er noch ein kleines Gert ausgehndigt, einen NFC-Tag, welchen er lediglich vor ein Gemlde halten muss um Informationen zu diesem auf sein Handy zu bekommen. Auf dem NFC-Tag befindet sich eine Nummer, die Simon auf der Website des Museums eingibt. Nun verbindet er sein Smartphon mit seinen neuen Kopfhrern und steckt es in seine Hosentasche. Sobald Simon nun ein interessantes Gemlde sieht, ber das er mehr erfahren mchte, hlt er lediglich den NFC-Tag an das, vor dem Gemlde stehende, Lesegert und ber seine Kopfhrern beginnt eine Stimme ber das Gemlde zu reden.")]),_c('h2',{staticClass:"title-text jump-item",attrs:{"id":"vorraussetzungen"}},[_vm._v("Voraussetzungen, um das Projekt lokal aufzusetzen")]),_c('ul',{staticClass:"listWithoutTable"},[_c('li',[_vm._v("ein Microcontroller mit NFC-Reader")]),_c('li',[_vm._v("ein WLAN-fhiges Gert mit Zugang zu einem Browser (Idealerweise ein Smartphone)")]),_c('li',[_vm._v("ein NFC-Tag des Lucas Cranach Museums")])]),_c('h2',{staticClass:"title-text jump-item",attrs:{"id":"projektlokalaufsetzen"}},[_vm._v("Projekt lokal aufsetzen")]),_c('ol',{staticClass:"listWithoutTable"},[_c('li',[_vm._v("Repository klonen")]),_c('li',[_vm._v("Ins Verzeichnis \"frontend-website\" gehen")]),_c('li',[_vm._v("Command \"npm install\" ausfhren")]),_c('li',[_vm._v("Command \"npm run serve\" ausfhren")]),_c('li',[_vm._v("Code im Verzeichnis \"painting-microcontroller\" auf den Microcontroller laden (gegebenenfalls vorher die")]),_c('li',[_vm._v("Variablen \"ssID\" und \"pass\" im Code anpassen, um sich mit dem WLAN verbinden zu knnen)")]),_c('li',[_vm._v("Microcontroller starten")])]),_c('p',{staticClass:"hint-text"},[_vm._v("Nun kann die Website geffnet werden. Geben Sie anschlieend die Nummer auf der Rckseite des NFC-Tags ein (Zu Testzwecken nehmen Sie unseren konfigurierten NFC-Tag und geben Sie \"100\" ein) und drcken auf \"Ok\". Nun halten Sie Ihr NFC-Tag an den NFC-Reader und erfahren Sie mehr ber die Gemlde von Lucas Cranach...")]),_c('h2',{staticClass:"headline jump-item",attrs:{"id":"konzept"}},[_vm._v("Konzept")]),_c('img',{attrs:{"src":require("../../../_material/konzept.png"),"alt":"konzept"}}),_c('h2',{staticClass:"title-text jump-item",attrs:{"id":"userjourney"}},[_vm._v("User Journey")]),_c('p',{staticClass:"hint-text"},[_vm._v("Als allererstes bekommt der Benutzer das Passwort fr das WLAN-Netzwerk des Museums und kann sich mit diesem verbinden. Als nchstes wird dem Benutzer ein NFC-Tag ausgehndigt. Auf diesem NFC-Tag befindet sich eine Nummer. Anschlieend ffnet er auf seinem Smartphone die Website des Museums und kann diese Nummer im Frontend eingeben. Bei seinem Museumsbesuch kann der Nutzer nun zu einem beliebigen Gemlde gehen. Vor jedem Gemlde ist ein Microcontroller mit einem NFC-Lesegert angebracht. Damit sich die URL mit spezifischen Informationen zu einem Gemlde ffnet, muss der Nutzer lediglich zu eben diesem gehen und kann seinen NFC-Tag vor den Microcontroller halten.")]),_c('h2',{staticClass:"title-text jump-item",attrs:{"id":"behindthescenes"}},[_vm._v("Behind the scenes")]),_c('p',{staticClass:"hint-text"},[_vm._v("Jeder NFC-Tag ist mit einer user_ID ausgestattet und jedes Gemlde mit einer painting_ID. Die Nummer, die der Nutzer am Anfang auf der Home-Seite eingibt und besttigt, ist die user_ID. Sobald ein Nutzer seinen NFC-Tag vor einen Microcontroller hlt, liest das jeweilige NFC-Lesegert die user_ID aus und published eine Nachricht mit dem topic user_ID und dem Inhalt painting_ID. Die Website, die auf dem Smartphone des Nutzers offen ist subscribed das topic user_ID Nun wei die Website welche URL sie ffnen muss:")]),_c('h2',{staticClass:"title-text jump-item",attrs:{"id":"urluebersicht"}},[_vm._v("URL bersicht")]),_c('table',[_c('tr',[_c('th',{staticClass:"url-column"},[_vm._v("URL")]),_c('th',[_vm._v("Inhalt")]),_c('th',[_vm._v("Beispiel")])]),_c('tr',[_c('td',[_vm._v("/")]),_c('td',[_vm._v("Home-Seite; hier kann der Benutzer die Nummer eingeben, die sich auf seinem Transponder befindet")]),_c('td',[_vm._v("https://dsterle.github.io/cranach_erweiterte_interaktion/")])]),_c('tr',[_c('td',[_vm._v("/waiting/:user_id")]),_c('td',[_vm._v("Waiting-Seite; hier wird der Benutzer aufgefordert seinen Transponder an einen NFC-Reader vor ein Gemlde zu halten; die user_id ist die Nummer, die der Benutzer auf der Home-Seite eingegeben und besttigt hat")]),_c('td',[_vm._v("https://dsterle.github.io/cranach_erweiterte_interaktion/#/waiting/100")])]),_c('tr',[_c('td',[_vm._v("/user/:user_id/painting/:painting_id")]),_c('td',[_vm._v("Painting-Seite; hier werden dem Benutzer Information ber das Gemlde mit der jeweiligen painting_id angezeigt und ber Lautsprecher abgespielt")]),_c('td',[_vm._v("https://dsterle.github.io/cranach_erweiterte_interaktion/#/user/100/painting/1")])]),_c('tr',[_c('td',[_vm._v("/docs")]),_c('td',[_vm._v("Docs-Seite; hier befindet sich die Dokumentation unseres Projekts")]),_c('td',[_vm._v("https://dsterle.github.io/cranach_erweiterte_interaktion/#/docs")])])]),_c('h2',{staticClass:"title-text jump-item",attrs:{"id":"zeitplan"}},[_vm._v("Zeitplan")]),_c('table',[_c('tr',[_c('th',[_vm._v("Thema")]),_c('th',[_vm._v("Zeitaufwand")]),_c('th',[_vm._v("Links")])]),_c('tr',[_c('td',[_c('ul',[_c('li',[_vm._v("Recherche ber die API Webbluetooth von Google")]),_c('li',[_vm._v("Erste Implementierungsversuche um sich aus dem Browser mit einem Bluetooth Device zu verbinden")])])]),_c('td',[_vm._v("2 Tage")]),_c('td',[_c('a',{attrs:{"href":"https://webbluetoothcg.github.io/web-bluetooth/"}},[_vm._v("https://webbluetoothcg.github.io/web-bluetooth/")])])]),_c('tr',[_c('td',[_c('ul',[_c('li',[