ables.com/id/ESP32-With-RFID-Access-Control/")])])]),_c('tr',[_c('td',[_c('ul',[_c('li',[_vm._v("Implementierung des NFC-Teils (reading, writing, checking)")])])]),_c('td',[_vm._v("3 Tage")]),_c('td',[_c('a',{attrs:{"href":"https://www.instructables.com/id/RFID-Tag-Reading-and-Writing-TfCD-Project/"}},[_vm._v("https://www.instructables.com/id/RFID-Tag-Reading-and-Writing-TfCD-Project/")])])]),_c('tr',[_c('td',[_c('ul',[_c('li',[_vm._v("Aneignung von Vue js")])])]),_c('td',[_vm._v("5 Tage")]),_c('td',[_c('a',{attrs:{"href":"https://vuejs.org/v2/guide/"}},[_vm._v("https://vuejs.org/v2/guide/")]),_c('br'),_c('a',{attrs:{"href":"https://www.vuemastery.com/courses/intro-to-vue-js/vue-instance/"}},[_vm._v("https://www.vuemastery.com/courses/intro-to-vue-js/vue-instance/")])])]),_c('tr',[_c('td',[_c('ul',[_c('li',[_vm._v("Implementierung des Frontend-Teils")])])]),_c('td',[_vm._v("8 Tage")]),_c('td')]),_c('tr',[_c('td',[_c('ul',[_c('li',[_vm._v("Recherche ber Mglichkeit auf Buttons von Kopfhrern zu reagieren")])])]),_c('td',[_vm._v("2 Tage")]),_c('td',[_c('a',{attrs:{"href":"https://github.com/gnaudio/jabra-browser-integration"}},[_vm._v("https://github.com/gnaudio/jabra-browser-integration")]),_c('br'),_c('a',{attrs:{"href":"https://stackoverflow.com/questions/53467169/bluetooth-headphones-button-event-detection-in-javascript "}},[_vm._v("https://stackoverflow.com/questions/53467169/bluetooth-headphones-button-event-detection-in-javascript")]),_c('br'),_c('a',{attrs:{"href":"https://developer.mozilla.org/en-US/docs/Archive/B2G_OS/API/Audio_Channels_API/Using_the_AudioChannels_API"}},[_vm._v("https://developer.mozilla.org/en-US/docs/Archive/B2G_OS/API/Audio_Channels_API/Using_the_AudioChannels_API")])])]),_c('tr',[_c('td',[_c('ul',[_c('li',[_vm._v("Deployment der Frontend-Website auf Github Pages")])])]),_c('td',[_vm._v("2 Tage")]),_c('td',[_c('a',{attrs:{"href":"https://medium.com/@Roli_Dori/deploy-vue-cli-3-project-to-github-pages-ebeda0705fbd"}},[_vm._v("https://medium.com/@Roli_Dori/deploy-vue-cli-3-project-to-github-pages-ebeda0705fbd")]),_c('br'),_c('a',{attrs:{"href":"https://cli.vuejs.org/guide/deployment.html"}},[_vm._v("https://cli.vuejs.org/guide/deployment.html")])])]),_c('tr',[_c('td',[_c('ul',[_c('li',[_vm._v("Bug Fixing")])])]),_c('td',[_vm._v("3 Tage")]),_c('td')])]),_c('p',{staticClass:"hint-text"},[_vm._v("Die angegebenen Tage Zeitaufwand sind eine Schtzung und dienen lediglich zur bersicht.")]),_c('h2',{staticClass:"headline jump-item",attrs:{"id":"entscheidungslogbuch"}},[_vm._v("Entscheidungslogbuch")]),_c('h2',{staticClass:"entscheidungs-headline removeBorder"},[_vm._v("QR")]),_c('p',{staticClass:"hint-text"},[_vm._v("Der Nutzer soll Informationen ber ein Gemlde auf seinem Smartphone angezeigt bekommen. Unsere Idee ist ein QR-Code neben den Gemlden, der eine URL spezifiziert. Sobald man diesen scannt wird auf dem Smartphone die URL geffnet.")]),_c('p',{staticClass:"sub-text"},[_vm._v("Wir haben uns gegen diese Idee entschieden, da zum einen nicht jeder Nutzer einen QR-Code Scanner installiert hat und zum anderen die User Journey sehr kompliziert ist: QR-Code Scanner suchen vor ein Gemlde halten, eventuell ist das Licht schlecht oder andere Leute versperren den Weg usw...")]),_c('h2',{staticClass:"entscheidungs-headline"},[_vm._v("Idee Bluetooth Beacons")]),_c('p',{staticClass:"hint-text"},[_vm._v("Man kann mithilfe des Webbrowsers nach BLE Beacons scannen, ohne eine App instaliert haben zu mssen. Der Benutezr soll also nur eine Website ffnen und somit nach in der Nhe liegenden Beacons scannen knnen.")]),_c('p',{staticClass:"sub-text"},[_vm._v("\n      Das Problem ist, dass die API mit der wir arbeiten (webbluetooth) noch nicht vollstndig in den Browsern implementiert ist. Man kann sich bis jetzt nur aus dem Browser heraus mit anderen Bluetooth Gerten verbinden, jedoch nicht nach Beacons scannen. Die Funktionalitt ist allerdings schon in der webbluetooth API dokumentiert, wird allerdings noch in den Browsern implementiert.\n      "),_c('br'),_c('a',{attrs:{"href":"https://webbluetoothcg.github.io/web-bluetooth/scanning.html"}},[_vm._v("https://webbluetoothcg.github.io/web-bluetooth/scanning.html")])]),_c('h2',{staticClass:"entscheidungs-headline"},[_vm._v("Javascript Framework VueJS")]),_c('p',{staticClass:"hint-text"},[_vm._v("Das Problem ist, dass wir fr eine Reihe aus Gemlden einen hnlichen Screen erzeugen wollen. Das Bild soll immer an der selben Stelle stehen, der Titel soll immer gleich zentriert sein usw... Also brauchen wir eine Technologie, mit der man Screenelemente generalisieren kann und auf unterschiedlichen Content bertragen kann, ohne jedes mal das Design und die Funktionen neu zu erstelen.")]),_c('p',{staticClass:"sub-text"},[_vm._v("Wir haben uns fr VueJS entschieden, da wir bereits in der Vorlesung einen Workshop dazu gemacht hatten und uns ein wenig genauer in dieses Framework hineinlesen wollen. Des Weiteren hat uns die Dokumentation von VueJS sehr zugesagt. Zur Auswahl stand noch ReactJS, welches fr unsere Zwecke jedoch etwas zu umfangreich ist.")]),_c('h2',{staticClass:"entscheidungs-headline"},[_vm._v("Designentscheidungen")]),_c('p',{staticClass:"hint-text"},[_vm._v("\n      Wir haben uns fr einen Mobile First Ansatz entschieden, da der Nutzer im Museum im wahrscheinlichsten Fall die Anwendung mit dem Smartphone bedient. Unsere Anwendung ist darauf ausgelegt, dass der Nutzer im Idealfall sein Smartphone in der Hosentasche lassen kann und sich die spezifischen Informationen zu einem Gemlde ber Kopfhrer anhren kann, indem er lediglich den NFC-Tag vor einen Microcontroller hlt. Der zweite Anwendungsfall wre, dass der Nutzer sich lieber die Informationen durchlesen mchte. Fr diesen Fall werden auf der Website nicht nur Audiodateien abgespielt, sondern die Informationen werden auch visuell dargestellt. Unsere Anwendung ist derzeit noch nicht responsive, wre aber, sollten wir das Projekt ber lngeren Zeitraum verfolgen, eine berlegung wert (eventuell hat der Nutzer ein iPad, oder eine Smartwatch, ...). Mockups und unseren Styleguide finden Sie hier:\n      "),_c('br'),_c('a',{attrs:{"href":"https://www.figma.com/file/FNNS1oMtWXAHqQP4qxJbVq/Cranach-erweiterte-Interaktion?node-id=0%3A1"}},[_vm._v("https://www.figma.com/file/FNNS1oMtWXAHqQP4qxJbVq/Cranach-erweiterte-Interaktion?node-id=0%3A1")])]),_c('h2',{staticClass:"entscheidungs-headline"},[_vm._v("Idee NFC")]),_c('p',{staticClass:"hint-text"},[_vm._v("Unsere Idee ist, dass der Nutzer bei einem Museumsbesuch sein Handy in der Hosentasche lassen kann und mit diesem nur ber Kopfhrer verbunden ist. Sobald der Nutzer nun ein externes Gert oder einen Chip vor ein Gemlde hlt, so ffnet sich im Internetbrowser eine Seite und eine Audiodatei beginnt zu spielen. Der Nutzer soll hierbei keine Anwendung extra installieren mssen, sondern lediglich die Website des Cranach Archivs aufrufen mssen.")]),_c('p',{staticClass:"sub-text"},[_vm._v("Unsere Idee ist dieses Konzept mit NFC umzusetzen, bei der der Nutzer einen NFC-Tag vom Museum erhlt und mit diesen vor ein Gemlde halten kann, an welchen Microcontroller mit NFC-Lesegerten installiert sind.")]),_c('h2',{staticClass:"entscheidungs-headline"},[_vm._v("MQTT als Protokoll zur Umsetzung des Publish-Subscribe Paradigmas")]),_c('p',{staticClass:"hint-text"},[_vm._v("Message Queuing Telemetry Transport (MQTT) ist ein leichtgewichtiges offenes Nachrichtenprotokoll, welches Machine-to-Machine-Kommunikation ermglicht. Es wurde entwickelt, um Nachrichten effizient zu transportieren und dabei ressourcensparend zu sein, weshalb es sich sehr gut fr den IOT-Bereich eignet.")]),_c('p',{staticClass:"sub-text"},[_vm._v("Wir haben uns fr dieses Protokoll entschieden, da es fr uns das geeignetste Protokoll zu sein scheint, um unseren Use Case zu erfllen. In unserem System werden lediglich minimale Informationen ausgetauscht und das auch nur, wenn eine Vernderung eintritt (User hlt NFC Tag an das Gemlde). Daher eignet sich zunchst einmal das Publish-Subscribe Paradigma fr die Datenbertragung sehr gut. MQTT wurde extra fr dieses Paradigma entwickelt und ermglicht durch seine TLS-Erweiterung eine sichere bertragung. Durch die lose Kopplung zwischen den Kommunikationspartnern kann eine schnelle Datenbertragung ermglicht werden, auch zwischen mehreren Kommunikationspartnern.")]),_c('h2',{staticClass:"entscheidungs-headline"},[_vm._v("howler.js als Javascript Framework fr Audiomanipulation")]),_c('p',{staticClass:"sub-text"},[_vm._v("Wir haben uns innerhalb des Kurses \"Frontend-Development 1\" mit einem Javascript Framework names howler.js beschftigt, mit welchem man Audiodateien abspielen und manipulieren kann. Wir benutzen dies ebenfalls in unserem Projekt um die Audios ber die Gemldeinformationen abspielen zu knnen.")]),_c('h2',{staticClass:"headline jump-item",attrs:{"id":"browserkompatibilitaet"}},[_vm._v("Browserkompatibilitt")]),_c('table',[_c('tr',[_c('th',[_vm._v("Browser")]),_c('th',[_vm._v("Noch zu implementieren")])]),_c('tr',[_c('td',[_vm._v("Google Chrome")]),_c('td',[_vm._v("Audiodateien werden in seltenen Fllen nicht automatsich abgespielt")])]),_c('tr',[_c('td',[_vm._v("Mozilla Firefox")]),_c('td',[_vm._v("verbindet sich nicht mit dem MQTT-Broker")])]),_c('tr',[_c('td',[_vm._v("Microsoft Edge")]),_c('td',[_vm._v("Audiodateien werden in seltenen Fllen nicht automatsich abgespielt")])]),_c('tr',[_c('td',[_vm._v("Opera")]),_c('td',[_vm._v("Audiodateien werden in seltenen Fllen nicht automatsich abgespielt")])]),_c('tr',[_c('td',[_vm._v("Safari")]),_c('td',[_vm._v("Audiodateien werden in seltenen Fllen nicht automatsich abgespielt")])])]),_c('p',{staticClass:"hint-text"},[_vm._v("\n      Wir haben es leider nicht geschafft diese Punkte zu debuggen, da wir uns ebenfalls noch mit dem Themenbereich \"Deployment von Vue-Anwendungen\" beschftigen wollten und somit eine Github Page fr unser System angelegt haben:\n      "),_c('br'),_c('a',{attrs:{"href":"https://dsterle.github.io/cranach_erweiterte_interaktion/"}},[_vm._v("https://dsterle.github.io/cranach_erweiterte_interaktion/")])]),_c('h2',{staticClass:"headline jump-item",attrs:{"id":"offenepunkte"}},[_vm._v("Offene Punkte")]),_c('p',{staticClass:"hint-text"},[_vm._v("Die folgenden Punkte haben wir aus Zeit- oder anderweitigen Grnden nicht mehr in das Projekt integrieren knnen:")]),_c('ul',{staticClass:"listWithoutTable"},[_c('li',[_vm._v("Backend der Website (REST-Architektur, Datenbank etc.); hier sollen die Informationen ber Gemlde und die Audiodateien gespeichert werden")]),_c('li',[_vm._v("Responsivitt der Website (z.B. Tablet und Desktop)")]),_c('li',[_vm._v("Verschiedene Modi fr die Website (z.B. Auswahl zwischen automatischem- und manuellem Abspielen der Audios)")]),_c('li',[_vm._v("Erstellung eines eigenen MQTT-Brokers")]),_c('li',[_vm._v("Kompatibilitt fr alle Browser")]),_c('li',[_vm._v("In unserer URL steht im Moment noch ein #, da unsere Vue Anwendung alles in die index.html hineinrendert. Der nchste Schritt wre mithilfe von Nuxt.js mehrere html Seiten rendern zu knnen")])]),_c('h2',{staticClass:"headline jump-item",attrs:{"id":"fazit"}},[_vm._v("Fazit")]),_c('p',{staticClass:"hint-text"},[_vm._v("\n      Zum Ende hin stellt sich die Frage, ob wir unser anfnglich gestelltes Ziel erreicht haben. Die Antwort lautet: Ja! Unser Ziel war es ein System zu entwickeln, welches den Museumsbesuchern die Mglichkeit gibt, die Gemlde des Museums zu betrachten und sich zustzliche Informationen anzueignen ohne dafr jedes Mal auf das Handy schauen zu mssen. Die Besucher mssen dafr lediglich ihren Transponder vor ein Gemlde (NFC-Reader) halten, die an das Smartphone angeschlossenen Kopfhrer einsetzen und das Handy kann in der Tasche bleiben.\n      Zwar haben wir ursprnglich verschiedene Anstze probiert (wie Sie im Entscheidungs-logbuch und im Zeitplan sehen knnen), sind jedoch der Ansicht die bestmgliche Umsetzung fr diesen Use Case gefunden zu haben.\n    ")]),_c('h2',{staticClass:"title-text jump-item",attrs:{"id":"problemebeiderentwicklung"}},[_vm._v("Probleme bei der Entwicklung")]),_c('p',{staticClass:"hint-text"},[_vm._v("\n      Eine groe Herausforderung war die Aneignung von Vuejs. Da wir dieses Thema lediglich einmal in der Vorlesung behandelt haben, hat uns dieser Teil viel Zeit gekostet. Jedoch konnten wir dadurch unsere Kompetenzen in der Selbstaneignung von Wissen enorm verbessern.\n      Abgesehen davon, hatten wir Probleme bei der Einbindung des Microcontrollers in unser System, da dieses Themenfeld gro ist und wir uns auch hier viele Dinge selbst aneignen mussten. Auerdem haben wir viel Zeit verloren, da wir erst nach einiger Zeit feststellen mussten, dass unser NFC-Reader defekt war.\n    ")]),_c('h2',{staticClass:"title-text jump-item",attrs:{"id":"projektarbeit"}},[_vm._v("Projektarbeit")]),_c('p',{staticClass:"hint-text"},[_vm._v("Durch den intensiven Einsatz von Github in den vergangenen Monaten, konnten wir hervorragend kollaborativ arbeiten, ohne uns dafr extra verabreden zu mssen. Wir haben neue Mglichkeiten kennengelernt, die Github bereitstellt und uns bei der gemeinsamen Entwicklung enorm geholfen haben.")]),_c('p',{staticClass:"hint-text"},[_vm._v("Zusammengefasst knnen wir guten Gewissens sagen, dass wir im Rahmen dieses Projekts viele Dinge gelernt und neue Kompetenzen erworben haben, die uns im weiteren Verlauf unseres Studiums, aber auch im Berufsleben helfen werden.")])])])}]

